over;\n$border-warning: 1px solid $color-warning-border;\n$border-warning-hover: 1px solid $color-warning-border-hover;\n\n$border: 1px solid $color-gray-mid;\n$border-focus: 1px solid $color-primary;\n$box-shadow-focus: 0 0 0 2px $color-gray;\n$box-shadow-focus-primary: 0 0 0 1px $color-primary;\n\n$box-shadow: 0 0 28px 16px rgba(0, 0, 0, .1);\n$box-shadow-card: 0 0 24px 12px rgba(0, 0, 0, .02);\n$box-shadow-card-hover: 0 0 40px 12px rgba(0, 0, 0, .001);\n$box-shadow-hover: 0 0 0 .5px $color-gray-default;\n\n// Skeleton loading gradient\n$skeleton-gradient: linear-gradient(90deg, #1a1a1a 25%, #1d1d1d 50%, #1a1a1a 75%);\n\n@keyframes shimmer {\n    0% {\n      background-position: -200% 0;\n    }\n    100% {\n      background-position: 200% 0;\n    }\n  }\n\n@mixin skeleton-shimmer {\n  background: $skeleton-gradient;\n  background-size: 200% 100%;\n  animation: shimmer 2s linear infinite;\n  border-radius: 2px;\n}\n\n@mixin hide-scrollbar {\n    &::-webkit-scrollbar {\n        display: none; \n    }\n\n    scrollbar-width: none;\n    -ms-overflow-style: none; \n}\n\n@mixin bg-border($text-color: $color-white, $bg-color: $color-primary-bg, $border: $border-primary) {\n    color: $text-color;\n    background-color: $bg-color;\n    border: $border;\n    transition: all 0.2s ease-in-out;\n}\n\n@mixin bg-border-hover($text-color: $color-white, $bg-color: $color-primary-bg, $border: $border-primary, $hover-bg-color: $color-primary-bg-hover, $hover-border: $border-primary-hover) {\n    @include bg-border($text-color, $bg-color, $border);\n    &:hover {\n        background-color: $hover-bg-color;\n        border: $hover-border;\n        cursor: pointer;\n    }\n    &:focus, &:active {\n        border: 1px solid transparent;\n    }\n}\n\n@mixin bg-border-hover-gray($text-color: $color-white, $bg-color: $color-gray-dark, $border: $color-gray-default, $hover-bg-color: $color-black, $hover-border: $color-gray-mid) {\n    color: $text-color;\n    background-color: $bg-color;\n    border: 1px solid $border;\n    &:hover {\n        background-color: $hover-bg-color;\n        border-color: $hover-border;\n    }\n}\n\n@mixin bg-border-warning($text-color: $color-white, $bg-color: $color-warning-bg, $border: $border-warning) {\n    @include bg-border($text-color, $bg-color, $border);\n}\n\n@mixin bg-border-hover-warning($text-color: $color-white, $bg-color: $color-warning-bg, $border: $border-warning, $hover-bg-color: $color-warning-bg-hover, $hover-border: $border-warning-hover) {\n    @include bg-border-hover($text-color, $bg-color, $border, $hover-bg-color, $hover-border);\n}\n\n// Custom scrollbar mixin\n@mixin custom-scrollbar {\n  // Vertical scrollbar track\n  .simplebar-track.simplebar-vertical {\n    width: 8px;\n    background: transparent;\n    right: 0;\n  }\n\n  // Horizontal scrollbar track (if needed)\n  .simplebar-track.simplebar-horizontal {\n    height: 6px;\n    background: transparent;\n    bottom: 0;\n  }\n\n  // Scrollbar thumb styling\n  .simplebar-scrollbar {\n    position: relative;\n    background: $color-gray;\n    border-radius: 4px;\n    opacity: 0.1;\n    transition: opacity 0.4s ease 0.8s; // 0.8s delay before fading out\n    \n    &::before {\n      display: none;\n    }\n\n    // Only transition on hover, not when dragging stops\n    &:hover {\n      opacity: 0.6;\n      transition: opacity 0.2s ease;\n    }\n  }\n\n  .simplebar-visible {\n    opacity: 0.4;\n    transition: opacity 0s 0s;\n  }\n\n  &.simplebar-scrolling.simplebar-dragging {\n    .simplebar-scrollbar {\n      opacity: .8;\n    }\n  }\n}","@import '../../globals/variables';\n\n.performance-panel {\n  position: fixed;\n  top: 20px;\n  right: 20px;\n  width: 380px;\n  max-height: 80vh;\n  background: $color-black;\n  color: $color-white;\n  border-radius: 8px;\n  border: 1px solid $color-gray-mid;\n  box-shadow: $box-shadow;\n  z-index: 9999;\n  font-family: 'Raleway', sans-serif;\n  font-size: 14px;\n  overflow: hidden;\n\n  &__header {\n    displ