o', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;\n    color: $color-white;\n  }\n\n  &__metric-bar {\n    height: 6px;\n    background: $color-gray-mid;\n    border-radius: 3px;\n    overflow: hidden;\n  }\n\n  &__metric-fill {\n    height: 100%;\n    border-radius: 3px;\n    transition: width 0.3s ease, background-color 0.3s ease;\n  }\n\n  &__warning {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    padding: 12px 16px;\n    background: $color-warning-bg;\n    border: 1px solid $color-warning-border;\n    border-radius: 6px;\n    margin-bottom: 20px;\n  }\n\n  &__warning-icon {\n    font-size: 18px;\n  }\n\n  &__warning-text {\n    font-size: 13px;\n    color: $color-warning;\n    font-weight: 500;\n  }\n\n  &__alerts {\n    margin-bottom: 16px;\n  }\n\n  &__alerts-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 8px;\n  }\n\n  &__alerts-header h4 {\n    margin: 0;\n    font-size: 12px;\n    color: rgba(255, 255, 255, 0.9);\n  }\n\n  &__clear-alerts {\n    background: rgba(255, 255, 255, 0.1);\n    border: 1px solid rgba(255, 255, 255, 0.2);\n    color: white;\n    padding: 4px 8px;\n    border-radius: 3px;\n    font-size: 10px;\n    cursor: pointer;\n    transition: background-color 0.2s;\n\n    &:hover {\n      background: rgba(255, 255, 255, 0.2);\n    }\n  }\n\n  &__alerts-list {\n    display: flex;\n    flex-direction: column;\n    gap: 6px;\n    max-height: 120px;\n    overflow-y: auto;\n  }\n\n  &__alert {\n    padding: 6px 8px;\n    border-radius: 3px;\n    font-size: 10px;\n    border-left: 3px solid;\n\n    &--high-cpu {\n      background: rgba(255, 68, 68, 0.1);\n      border-left-color: #ff4444;\n    }\n\n    &--high-memory {\n      background: rgba(255, 170, 0, 0.1);\n      border-left-color: #ffaa00;\n    }\n\n    &--slow-render {\n      background: rgba(255, 170, 0, 0.1);\n      border-left-color: #ffaa00;\n    }\n\n    &--too-many-interva